import postRobot from '../../../post-robot-wrapper';
import './paypal.html';
import '../styles/defaults.css';
import {getComponentIdFromQueryString, getControllerIdFromQueryString} from '../querystring';
import {config} from '../config';
import {addHandleOptions, mountComponent} from '../payment-events';
import {
  emitComponentCancelled,
  emitComponentReady,
} from '../payment-api-events';
import {generateInstanceData} from '../create-initial-data';
import {generateComponentData} from '../payment-component-data';
import {handleEvent, sendEventData} from '../input-events';
import {onAuthorizeListener} from './paypal-controller-events';
import {getMappedLocale} from '../localization/localized-messages';

function getComponentNameFromID(componentID) {
  return componentID.substring(0, componentID.indexOf('-'));
}

function determinePayPalType() {
  const supportedTypes = [
    getComponentIdFromQueryString('paypal'),
    getComponentIdFromQueryString('paypalcredit')
  ];

  const componentID = supportedTypes.filter( type => type !== null)[0];
  return getComponentNameFromID(componentID);
}

const COMPONENT_TYPE = determinePayPalType();

const componentData = generateComponentData(COMPONENT_TYPE, getComponentIdFromQueryString(COMPONENT_TYPE),getControllerIdFromQueryString());

const controllerListener = postRobot.listener({ window: componentData.controller.window, domain: config.domain });
const controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });

const idOfContainer = '#paypal';

let paymentOptions = null;

const instanceData = generateInstanceData(
  controllerEmitter,
  componentData,
  getPaymentOptions,
  getElement,
  setOptions,
  undefined
);

mountComponent(controllerEmitter, componentData, handleOptions, emitComponentReady, undefined, instanceData);
addHandleOptions(controllerListener, handleOptions);
onAuthorizeListener(controllerListener, componentData, onMockPageAuthorizeEvent, sendCloseDialogEvent, handleCancel);

/**
 * returns the element of the paypal div
 * @returns {HTMLElement}
 */
export function getElement() {
  return document.getElementById('paypal');
}

/**
 * Returns payment options
 * @returns {object}
 */
export function getPaymentOptions() {
  return paymentOptions;
}

export function getComponentData() {
  return componentData;
}

function createSource(paymentServiceSourceData) {
  return controllerEmitter.send('createSourceFromRequest', {
    componentId: componentData.componentId,
    componentType: componentData.componentType,
    sourceRequest: paymentServiceSourceData,
    type: componentData.componentType
  }).then( res => res.data);
}

function isTestMode() {
  return paymentOptions.isTestApiKey;
}

function shouldDisplayMockPage() {
  return paymentOptions.shouldDisplayMockPage;
}

export function sendCloseDialogEvent() {
  sendCloseWindowEventToController();
}

function getButtonEnv() {
  return isTestMode() ? 'sandbox' : 'production';
}

export function onPaymentEvent() {

  const paymentServiceSourceData = getPaymentOptions().options.sourceData;

  return createSource(paymentServiceSourceData).then(function (result) {
    if (result.error) {
      sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', result);
      return '';
    } else {
      setSourceId(result.source.id);
      setClientSecret(result.source.clientSecret.split('_')[1]);
      return result.source[getType(paymentServiceSourceData)].token;
    }

  });
}

function getType(paymentServiceSourceData) {
  return typeof paymentServiceSourceData.type !== 'undefined' ? paymentServiceSourceData.type : 'payPal';
}

export function onValidateEvent(actions) {
  actions.disable();
}

export function setSourceId(sourceId) {
  paymentOptions.sourceId = sourceId;
}

export function setClientSecret(clientSecret) {
  paymentOptions.clientSecret = clientSecret;
}

function getClientSecretFromSource(source) {
  return source.clientSecret.split('_')[1];
}

export function onProductionClickEvent() {

  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'click', {});

  return Promise.resolve();

}

export function onMockPageClickEvent() {

  const paymentServiceSourceData = getPaymentOptions().options.sourceData;

  sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'click', {});

  return createSource(paymentServiceSourceData).then(function (result) {
    if (result.error) {
      sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', result);
    } else {
      setSourceId(result.source.id);
      setClientSecret(getClientSecretFromSource(result.source));

      sendOpenWindowEventToController(result.source.id);

      return result.source[getType(paymentServiceSourceData)].token;
    }

  });

}

export function onProductionAuthorizeEvent() {
  const paypalSourceId = getPaymentOptions().sourceId;
  const clientSecret =  getPaymentOptions().clientSecret;

  return sendGetSourceRequestToController(paypalSourceId, clientSecret).then( (sourceResult) => {
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', sourceResult);
  });
}

export function onMockPageAuthorizeEvent() {
  const paypalSourceId = getPaymentOptions().sourceId;
  const clientSecret =  getPaymentOptions().clientSecret;

  return sendGetSourceRequestToController(paypalSourceId, clientSecret).then( (sourceResult) => {
    sendCloseDialogEvent();
    sendEventData(componentData.controller, componentData.componentId, componentData.componentType, 'source', sourceResult);
  });
}

export function onCancelEvent() { // event: cancel
  handleCancel(componentData);
}

function getLocale(instanceOptions) {
  const mappedLocale = getMappedLocale(instanceOptions.locale);
  return mappedLocale.replace('-', '_');
}

export function getMockPageButtonConfiguration(configuration, instanceOptions) {
  configuration.locale = getLocale(instanceOptions);
  configuration.env = getButtonEnv();
  configuration.payment = onPaymentEvent;
  configuration.validate = onValidateEvent;
  configuration.onClick = onMockPageClickEvent;
  configuration.onCancel = onCancelEvent;
  configuration.onAuthorize = onMockPageAuthorizeEvent;
  return configuration;
}

export function getProductionButtonConfiguration(configuration, instanceOptions) {
  configuration.locale = getLocale(instanceOptions);
  configuration.env = getButtonEnv();
  configuration.payment = onPaymentEvent;
  configuration.onClick = onProductionClickEvent;
  configuration.onCancel = onCancelEvent;
  configuration.onAuthorize = onProductionAuthorizeEvent;
  return configuration;
}

export function getPayPalReceiverUrl(action, type) {
  return window.location.href.replace('/paypal/', '/paypal-receiver/').replace('paypal.html', 'paypal-receiver.html')+ '&action=' + action+'&type='+type;
}

export function getConfigurationBasedOnAPIKeyType(shouldDisplayMockPage, configuration, instanceOptions) {
  if (shouldDisplayMockPage) {
    return getMockPageButtonConfiguration(configuration, instanceOptions);
  } else {
    return getProductionButtonConfiguration(configuration, instanceOptions);
  }
}

export function mergePayPalCreditStyleWithDefault(style) {
  const defaultStyle = {
    label: 'credit',
    size: 'responsive',
    shape: 'rect',
    layout: 'horizontal',
    tagline: 'false'
  };

  return Object.assign({}, defaultStyle, style);
}


export function mergePayPalStyleWithDefault(style) {

  const defaultStyle = {
    label: '',
    size: 'responsive',
    color: 'gold',
    shape: 'pill',
    layout: 'horizontal',
    fundingicons: 'false',
    tagline: 'false'
  };

  return Object.assign({}, defaultStyle, style);
}

/**
 * Add the button for paypal
 */
export function addButton(options, instanceOptions) {

  const configuration = {
    style: options.style,
  };

  if (COMPONENT_TYPE === 'paypalcredit') {
    configuration.style = mergePayPalCreditStyleWithDefault(configuration.style);

    configuration.funding = {
      allowed: [window.paypal.FUNDING.CREDIT],
      disallowed: [window.paypal.FUNDING.CARD]
    };

    delete configuration.style.color;
    delete configuration.style.fundingicons;
  } else {

    configuration.funding = {
      allowed: [window.paypal.FUNDING.CARD],
      disallowed: [window.paypal.FUNDING.CREDIT]
    };

    configuration.style = mergePayPalStyleWithDefault(configuration.style);
  }

  const updatedConfiguration = getConfigurationBasedOnAPIKeyType( shouldDisplayMockPage(), configuration, instanceOptions);

  if (getElement().childNodes.length > 0) {
    getElement().innerHTML = '';
  }


  window.paypal.Button.render(updatedConfiguration, idOfContainer);
  instanceData.buttonStyle = updatedConfiguration.style;
}

/**
 * Sends cancel event data to controller to be sent on to client
 */
export function handleCancel(componentData) {
  emitComponentCancelled(componentData);
}

function isObjectShallowEqual(o1, o2) {
  return JSON.stringify(o1) === JSON.stringify(o2);
}

function setRedirectValues(data) {

  const paymentServiceSourceData = data.options.sourceData;
  paymentServiceSourceData[getType(paymentServiceSourceData)].returnUrl = getPayPalReceiverUrl('return', COMPONENT_TYPE);
  paymentServiceSourceData[getType(paymentServiceSourceData)].cancelUrl = getPayPalReceiverUrl('cancel', COMPONENT_TYPE);

  return data;
}

/**
 * Applies options to google pay element
 * @param {object} data
 */
export function handleOptions(data) {

  const isStyleEqual = paymentOptions === null ? false : isObjectShallowEqual(data.options.style, paymentOptions.options.style);

  setOptions(data);

  if (!isStyleEqual) {
    addButton(data.options, data.instanceOptions);
  }
}

function setOptions(unsafeOptions) {
  unsafeOptions = setRedirectValues(unsafeOptions);
  paymentOptions = unsafeOptions;
}

export function addInputHtmlToDom() {
  document.body.innerHTML += inputHtml();
}

export function inputHtml() {
  return '<div id="paypal"></div>';
}

function sendGetSourceRequestToController(sourceId, sourceClientSecret) {
  return controllerEmitter.send('retrieveSourceFromComponent', {
    sourceId: sourceId,
    sourceClientSecret: sourceClientSecret
  }).then( res => res.data);
}

function sendOpenWindowEventToController(sourceId) {
  return controllerEmitter.send('openRedirectWindow', {
    url: config.paypalRedirectBaseUrl + sourceId,
  });
}

function sendCloseWindowEventToController() {
  return controllerEmitter.send('closeRedirectWindow', {});
}

export function resizeIframe(paypalDivSize) {
  let event = {
    detail: {
      frame: {
        height: paypalDivSize,
        id: componentData.componentId
      }
    }
  };
  handleEvent(componentData, 'resize', event);
}