export function onAuthorizeListener(controllerListener, componentData, onAuthorizeEvent, sendCloseDialogEvent, handleCancel) {
  controllerListener.on('authorize', (event) => {

    const {action} = event.data;

    if (action === 'return') {
      onAuthorizeEvent();
    } else {
      sendCloseDialogEvent();
      handleCancel(componentData);
    }

    return Promise.resolve();
  });
}